import React from 'react'
import MenuItemRenderer from '../MenuItemRenderer'

const DataStructureSideBar = () => {
  const sortMenu = [
    { id: 1, name: 'Stack (Array)', to: '/stack-array' },
    { id: 2, name: 'Stack (LL)', to: '/stack-linked-list' },
    { id: 3, name: 'Queue (Array)', to: '/queue-array' },
    { id: 4, name: 'Queue (LL)', to: '/queue-linked-list' },
    { id: 5, name: 'Circular Queue (Array)', to: '/circular-queue-array' },
    { id: 6, name: 'Circular Queue (LL)', to: '/circular-queue-link-list' },
    { id: 7, name: 'Priority Queue', to: '/priority-queue' }
  ]
  const sortList = sortMenu.map((item) => (
    <MenuItemRenderer key={item.id} item={item} />
  ))
  return <ul>{sortList}</ul>
}

export default DataStructureSideBar
