import React from 'react'
import { HashRouter, Route, Switch } from 'react-router-dom'

import './App.css'

import Navbar from './components/Navbar'
import Footer from './components/Footer'

import Home from './components/home/Home'
import DataStructure from './components/data_structure/DataStructure'
import Sort from './components/sort/Sort'
import Search from './components/search/Search'
import GeneticAlgorithm from './components/genetic_algorithm/GeneticAlgorithm'
import DeepLearning from './components/deep_learning/DeepLearning'

import BubbleSort from './components/sort/bubble_sort/BubbleSort'
import SelectionSort from './components/sort/selection_sort/SelectionSort'

import AStarSearch from './components/search/a_star_search/AStarSearch'

function App() {
  return (
    <HashRouter>
      <div className="App">
        <Navbar />
        <div className="container-fluid">
          <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/data-structure" component={DataStructure} />
            <Route path="/sort" component={Sort} />
            <Route path="/bubble-sort" component={BubbleSort} />
            <Route path="/selection-sort" component={SelectionSort} />
            <Route path="/search" component={Search} />
            <Route path="/a-star-search" component={AStarSearch} />
            <Route path="/genetic-algorithm" component={GeneticAlgorithm} />
            <Route path="/deep-learning" component={DeepLearning} />
          </Switch>
        </div>
        <Footer />
      </div>
    </HashRouter>
  )
}

export default App
