import React from 'react'
import { NavLink } from 'react-router-dom'

// import styles from '../components/Navbar.module.css'

const Navbar = () => {
  return (
    <div>
      <nav className="navbar navbar-expand-lg navbar-dark">
        <NavLink className="navbar-brand" to="/">
          Algo Dream
        </NavLink>
        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav m-auto">
            <li className="nav-item active">
              <NavLink className="nav-link ml-3" to="/">
                Home
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link ml-3" to="/data-structure">
                Data Structure
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link ml-3" to="/sort">
                Sort
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link ml-3" to="/search">
                Search
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link ml-3" to="/genetic-algorithm">
                Genetic Algorithm
              </NavLink>
            </li>
            <li className="nav-item">
              <NavLink className="nav-link ml-3" to="/deep-learning">
                Deep Learning
              </NavLink>
            </li>
          </ul>
          {/* <form className="form-inline my-2 my-lg-0">
            <input
              className="form-control mr-sm-2"
              type="search"
              placeholder="Search"
              aria-label="Search"
            />
            <button
              className="btn btn-outline-success my-2 my-sm-0"
              type="submit"
            >
              Search
            </button>
          </form> */}
        </div>
      </nav>
    </div>
  )
}

export default Navbar
