import React, { Component } from 'react';
import p5 from 'p5';

import SortingSideBar from '../SortingSideBar';
import SortConfig from '../SortConfig';

class BubbleSort extends Component {
	constructor() {
		super();
		this.myP5 = null;
		this.canvasRef = React.createRef();
		this.canvasWidth = null;
		this.canvasHeight = null;

		this.arr = [];
		this.normArr = [];

		this.sketch = (p) => {
			p.setup = () => {
				p.createCanvas(this.canvasWidth, this.canvasHeight).parent(
					this.canvasRef.current
				);
				p.noLoop();
			};
			p.draw = () => {
				p.background(255);

				let barWidth = p.width / this.normArr.length - 1;
				console.log(barWidth);
				for (let i = 0; i < this.normArr.length; i++) {
					let x0 = i * (p.width / this.normArr.length);
					let y0 = p.height;
					let barHeight = -this.normArr[i];

					p.noStroke();
					p.fill('rgba(37,188,255, 1)');
					p.rect(x0, y0, barWidth, barHeight, 0, 0, 2, 2);

					if (this.arr.length <= 150) {
						p.fill(100);
						p.textSize(barWidth / 2);
						p.textAlign(p.CENTER, p.TOP);
						p.text(
							this.arr[i],
							x0 + barWidth / 2,
							y0 + barHeight - p.textSize()
						);
					}
				}

				//console.log('onee')
			};

			p.initializeRandomValue = (size, min, max) => {
				this.arr = [];
				for (let i = 0; i < size; i++) {
					this.arr.push(p.floor(p.random(min, max + 1)));
				}
				console.log(this.arr);
				this.normArr = this.arr.map((x) =>
					p.floor(p.map(x, min, max, 1, p.height - 200, true))
				);
				console.log(this.normArr);
				p.redraw();
			};
		};
	}

	handleRandom = (size, min, max) => {
		this.myP5.initializeRandomValue(size, min, max);
	};
	componentDidMount() {
		//this.setState({ isRandom: true })
		let divInfo = document
			.getElementById('drawing-canvas')
			.getBoundingClientRect();
		this.canvasWidth = divInfo.width - 20;
		this.canvasHeight = divInfo.height;
		this.myP5 = new p5(this.sketch);
	}

	render() {
		return (
			<div className='row'>
				<div className='col-2 side-bar'>
					<SortingSideBar />
				</div>
				<div className='col-10 main-content'>
					<SortConfig
						title='Bubble Sort'
						initRandom={this.handleRandom}
					/>
					<div className='row mt-3 drawing-canvas'>
						<div
							className='col'
							id='drawing-canvas'
							ref={this.canvasRef}
						></div>
					</div>
				</div>
			</div>
		);
	}
}

export default BubbleSort;
