import React, { Component } from 'react'
//import p5 from 'p5'

import SortingSideBar from '../SortingSideBar'
import SortConfig from '../SortConfig'

class SelectionSort extends Component {
  render() {
    return (
      <div className="row">
        <div className="col-2 side-bar">
          <SortingSideBar />
        </div>
        <div className="col-10 main-content">
          <SortConfig title="Selection Sort" />
          <div className="row">
            <div className="col" ref={this.myRef}></div>
          </div>
        </div>
      </div>
    )
  }
}

export default SelectionSort
