import React from 'react'
import MenuItemRenderer from '../MenuItemRenderer'

const SearchSideBar = () => {
  const sortMenu = [
    { id: 1, name: 'Binary Search', to: '/' },
    { id: 2, name: 'Binary Search Tree', to: '/' },
    { id: 3, name: 'Balanced BST', to: '/' },
    { id: 4, name: 'Breadth First Search', to: '/' },
    { id: 5, name: 'Depth First Search', to: '/' },
    { id: 6, name: 'Uniform Cost Search', to: '/' },
    { id: 7, name: 'Dijkstra Algorithm', to: '/' },
    { id: 8, name: 'Best-First Search', to: '/' },
    { id: 9, name: 'Greedy BFS', to: '/' },
    { id: 10, name: 'IDS', to: '/' },
    { id: 11, name: 'Depth Limit Search', to: '/' },
    { id: 12, name: 'Bidirectional Search', to: '/' },
    { id: 13, name: 'A* Search', to: '/a-star-search' }
  ]
  const sortList = sortMenu.map((item) => (
    <MenuItemRenderer key={item.id} item={item} />
  ))
  return <ul>{sortList}</ul>
}

export default SearchSideBar
