import React from 'react'
import MenuItemRenderer from '../MenuItemRenderer'

const SideBar = () => {
  const sortMenu = [
    { id: 1, name: 'Bubble Sort', to: '/bubble-sort' },
    { id: 2, name: 'Selection Sort', to: '/selection-sort' },
    { id: 3, name: 'Insertion Sort', to: '/insertion-sort' },
    { id: 4, name: 'Merge Sort', to: '/merge-sort' },
    { id: 5, name: 'Quick Sort', to: '/quick-sort' },
    { id: 6, name: 'Heap Sort', to: '/heap-sort' },
    { id: 7, name: 'Counting Sort', to: '/counting-sort' },
    { id: 8, name: 'Radix Sort', to: '/radix-sort' },
    { id: 9, name: 'Bucket Sort', to: '/bucket-sort' },
    { id: 10, name: 'Bogo Sort', to: '/bogo-sort' }
  ]
  const sortList = sortMenu.map((item) => (
    <MenuItemRenderer key={item.id} item={item} />
  ))
  return <ul>{sortList}</ul>
}

export default SideBar
