class Particle {
  constructor(p, Bodies, World, world, x, y, r, name, fixed, type) {
    const options = {
      friction: 0,
      restitution: 0.8,
      isStatic: fixed
    }
    this.p = p
    this.r = r
    this.name = name
    this.body = Bodies.circle(x, y, r, options)
    this.World = World
    this.world = world
    this.type = type
    World.add(world, this.body)
  }

  removeFromWorld() {
    this.World.remove(this.world, this.body)
  }
  offScreen() {
    let pos = this.body.position
    return pos.y > this.p.height
  }
  show() {
    let pos = this.body.position
    let angle = this.body.angle

    this.p.push()
    this.p.translate(pos.x, pos.y)
    this.p.rotate(angle)
    this.p.rectMode(this.p.CENTER)
    this.p.strokeWeight(1)
    this.p.stroke(100)
    if (this.type === 'r') {
      this.p.rect(0, 0, this.r * 2, 50)
    } else {
      this.p.ellipse(0, 0, this.r * 2)
    }
    this.p.pop()

    this.p.textSize(this.r / 3)
    this.p.textAlign(this.p.CENTER, this.p.CENTER)
    this.p.text(this.name, pos.x, pos.y)
  }
}

export default Particle
